<template>
  <main>
    <div class="menu">
      <div class="header">
        <div class="this-user-sneak">
          <div class="this-user-profile-pic">
            <img :src="this_user_profile_pic" referrerpolicy="no-referrer" />
          </div>
          <div class="this-user-sneak-data">
            <h3>{{ this_user_name }}</h3>

            <div class="dropdown">
              <button class="dropbtn fa fa-ellipsis-v"></button>
              <div class="dropdown-content">
                <button @click="handleSignOut">Logout</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="search">
        <div class="searchbar">
          <div class="searchicon">
            <button class="fa fa-search"></button>
          </div>
          <div class="searchbox">
            <input
              type="text"
              placeholder="search"
              v-model="user_filter"
              @input="showWork"
            />
          </div>
        </div>
      </div>

      <div class="userlist">
        <div
          class="user-sneak"
          v-for="users in computed_user_list"
          :key="users.id"
          @click="gotoChat(users.email, users.displayName, users.photoURL)"
        >
          <div class="user-profile-pic">
            <img :src="users.photoURL" referrerpolicy="no-referrer" />
          </div>
          <div class="user-sneak-data">
            <h3>{{ users.displayName }}</h3>
            <h5></h5>
          </div>
        </div>
      </div>
    </div>

    <div class="chat">
      <div class="header">
        <div class="current-chat-pic">
          <img :src="current_chat_profile" referrerpolicy="no-referrer" />
        </div>
        <div class="current-chat-name">
          <h3>{{ current_chat_user_name }}</h3>
        </div>
      </div>

      <div class="message">
        <div
          :class="item.message_origin"
          v-for="item in messageslist"
          :key="item.id"
        >
          <p>
            {{ item.message }}
            {{ item.time }}
          </p>
        </div>
      </div>

      <div class="chat-keypad">
        <input type="text" v-model="messageTosend" />
        <button
          @click="sendChat"
          :style="{
            background: messageTosend.length > 0 ? 'black' : 'grey',
            cursor: messageTosend.length > 0 ? 'pointer' : 'none',
          }"
        >
          <i class="fa fa-angle-double-right"></i>
        </button>
      </div>
    </div>
  </main>
</template>


<script setup>
// @ is an alias to /src
import { computed, onMounted, ref } from "vue";
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
import {
  collection,
  getDocs,
  query,
  onSnapshot,
  addDoc,
  Timestamp,
  orderBy,
} from "firebase/firestore";
import { useRouter } from "vue-router";
import db from "@/main";

// variables used throughout this page
const router = useRouter();

const isLoggedIn = ref(false);

// this user is "us" or the logged in user
const this_user_name = ref("");
const this_user_email = ref("");
const this_user_profile_pic = ref("");

// current_chat user is the user we are chatting with
const current_chat_user_name = ref("");
const current_chat_user_email = ref("");
const current_chat_profile = ref("");

const user_filter = ref("");

const messageslist = ref([]);
const messageTosend = ref("");
let userList = [];

let auth;
let q;
let snapshotListner;
let colref;
let chatref;

// list of all users
let computed_user_list = computed(() => {
  if (computed_user_filter.value == "") return userList;
  else {
    return userList.filter((userlistitem) => {
      return userlistitem.displayName
        .toLowerCase()
        .includes(computed_user_filter.value.toLowerCase());
    });
  }
});

// generates name for sub collection name for current user pair
// name pattern is x_to_y
// here x is lexicographically smaller than y
let subcolname = computed(() => {
  if (this_user_email.value > current_chat_user_email.value) {
    return current_chat_user_email.value + "_to_" + this_user_email.value;
  } else {
    return this_user_email.value + "_to_" + current_chat_user_email.value;
  }
});
const computed_user_filter = computed(() => {
  if (user_filter.value == "" || user_filter.value.trim().length == 0) {
    return "";
  } else return user_filter.value;
});

// function
// go to chat between selected user from userlist and us
function gotoChat(email, displayName, photoURL) {
  current_chat_user_name.value = displayName;
  current_chat_user_email.value = email;
  current_chat_profile.value = photoURL;

  console.log(subcolname.value);
  chatref = collection(db, "chats", subcolname.value, "chat_message");
  q = query(chatref, orderBy("time", "desc"));
  snapshotListner = onSnapshot(q, (snapshot) => {
    messageslist.value = [];
    snapshot.docs.forEach((doc) => {
      messageslist.value.push({
        message: doc.data().message,
        message_origin:
          doc.data().send_from == this_user_email.value
            ? "message-sent"
            : "message-recieved",
      });
    });

    console.log(messageslist.value);
  });
}
// send message written in text field to database
function sendChat() {
  addDoc(chatref, {
    send_from: this_user_email.value,
    send_to: current_chat_user_email.value,
    time: Timestamp.now(),
    message: messageTosend.value,
  }).then((messageTosend.value = ""));
}

onMounted(() => {
  auth = getAuth();

  this_user_name.value = auth.currentUser.displayName;
  this_user_profile_pic.value = auth.currentUser.photoURL;
  this_user_email.value = auth.currentUser.email;
  colref = collection(db, "users");

  getDocs(colref).then((snapshot) => {
    snapshot.docs.forEach((doc) => {
      // remove us from users list
      if (doc.data().email === auth.currentUser.email) {
      } else userList.push({ ...doc.data(), id: doc.id });
    });

    // initally go to chat between us and the first user in list
    gotoChat(userList[0].email, userList[0].displayName, userList[0].photoURL);
    user_filter.value = " ";
    user_filter.value = "";
  });

  chatref = collection(db, "chats", subcolname.value, "chat_message");
  q = query(chatref, orderBy("time", "desc"));

  // updates chat everytime any user sends new messgae
  snapshotListner = onSnapshot(q, (snapshot) => {
    messageslist.value = [];
    snapshot.docs.forEach((doc) => {
      messageslist.value.push({
        message: doc.data().message,
        message_origin:
          doc.data().send_from == this_user_email.value
            ? "message-sent"
            : "message-recieved",
      });
    });

    //console.log(messageslist.value);
  });

  onAuthStateChanged(auth, (user) => {
    if (user) {
      isLoggedIn.value = true;
    } else {
      isLoggedIn.value = false;

      // log out from home page is auth state is changed (logout )
      router.push("/LoginDashboard");
    }
  });
});

const handleSignOut = () => {
  signOut(auth).then(() => {
    router.push("/LoginDashboard");
  });
};
</script>

